import React, { Component } from 'react';
import { Row, Col, Menu, Icon, Form, Select } from 'antd';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class MenuTabTop extends Component {
  state = {};

  handleClick = e => {
    // console.log('click ', e);
    this.setState({
      current: e.key,
    });
    if (e.key === 'enteringInfo') {
      console.log('render');
    }
  };
  render() {
    const { Option } = Select;
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={24}>
            <Menu onClick={this.handleClick} selectedKeys={[this.state.current]} mode="horizontal">
              <Menu.Item key="enteringInfo">
                <Icon type="highlight" theme="twoTone" twoToneColor="#ff3333" />
                Tổng hợp lại điểm TBCMH
              </Menu.Item>
              <Menu.Item key="typeResults">
                <Select defaultValue="Phân loại kết quả học tập">
                  <Option value="1" href="#/student/summarizePoints/followSemester">
                    Kết quả học tập học kỳ
                  </Option>
                  <Option value="2" href="#/student/summarizePoints/followYear">
                    Kết quả học tập năm học
                  </Option>
                  <Option value="3" href="#/student/summarizePoints/followCourse">
                    Kết quả học tập toàn khóa
                  </Option>
                </Select>
              </Menu.Item>
              <Menu.Item key="printStopSchool">
                <Icon type="printer" theme="twoTone" twoToneColor="#6699ff" />
                In báo cáo
              </Menu.Item>
              <Menu.Item key="fileExcel">
                <Icon type="file-excel" theme="twoTone" twoToneColor="#00cc00" />
                Xuất Excel
              </Menu.Item>
            </Menu>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default MenuTabTop;



