import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import FormInfoCommonSemester from './FormInfoCommonSemester';
import MenuTabTop from './MenuTabTop';
import TableListSummarizeSemester from './TableListSummarizeSemester';
import './style.scss';

class ListSummarizePointsSemesterPage extends Component {
  state = {};
  render() {
    return (
      <Card title="Kết quả học tập">
        <Row className="rowMargin">
          <Col span={24}>
            <MenuTabTop />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <FormInfoCommonSemester />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableListSummarizeSemester />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListSummarizePointsSemesterPage />
  </Page>
);



